<template>
  <div :key="resetKey" class="run" @keydown.alt="resetSelf">
    <div class="countdown" :class="{started: countdownText === 'START'}">
      <h2> {{ countdownText }} </h2>
    </div>
    <main>
      <div class="top-bar">
        <div v-if="requestReset" class="requestReset">
          <h2>Room owner wants to start a new game</h2>
          <p>You will be moved to lobby</p>
          <button @click="$router.push('/')">
            OK
          </button>
          <button @click="disconnect">
            Leave room
          </button>
        </div>
        <div class="info">
          <div class="languageName">
            <h2>{{ languageName }}</h2>
          </div>
          <div class="codeInfo">
            <p v-if="codeInfo.name">
              {{ codeInfo.name }}.{{ language.ext }}
            </p>
            <p>{{ codeSource }}</p>
          </div>
        </div>
        <div class="buttons">
          <button class="reset" :disabled="room.connected" @click="reset">
            Reset
          </button>
          <button class="finish" :disabled="room.connected" @click="finish">
            Finish now
          </button>
        </div>
      </div>

      <!-- Changing key remounts component -->
      <CodeEditor
        v-if="language.name"
        ref="codeEditor"
        :key="componentKey"
        class="code-editor"
        @reset="reset"
        @completed="completed"
        @countdown="countdownChange"
      />
      <transition>
        <Results v-if="$route.path === '/results' && stats" :stats="stats" />
      </transition>
    </main>
  </div>
</template>

<script>
import CodeEditor from '@/components/CodeEditor.vue';
import Results from '@/views/Results.vue';
import { mapGetters } from 'vuex';

export default {
  name: 'Run',
  components: {
    CodeEditor,
    Results,
  },
  data() {
    return {
      componentKey: 1,
      resetKey: 1,
      stats: false,
      requestReset: false,
      countdownText: 3,
    };
  },
  computed: {
    ...mapGetters(['language', 'customCode', 'codeInfo', 'room']),
    codeSource() {
      if (this.codeInfo.name) {
        return this.codeInfo.source === 'own' ? 'ukasz Wielgus archive' : this.codeInfo.source;
      } if (this.room.connected && !this.room.owner) {
        return 'Code provided by room owner';
      }
      return 'Code provided by You';
    },
    languageName() {
      return this.language.name.replace('_', ' ');
    },
  },
  created() {
    if (!this.language.name) {
      this.$router.push('/');
    }
  },
  sockets: {
    reset() {
      this.requestReset = true;
    },
  },
  methods: {
    countdownChange(text) {
      this.countdownText = text;
    },
    reset() {
      this.componentKey += 1;
      if (this.$route.path === '/results') {
        this.$router.push('/run');
      }
    },
    resetSelf() {
      this.resetKey += 1;
      if (this.$route.path === '/results') {
        this.$router.push('/run');
      }
    },
    finish() {
      this.$refs.codeEditor.completed();
    },
    completed(stats) {
      this.stats = stats;
    },
    disconnect() {
      this.requestReset = false;
      this.$socket.client.disconnect();
      this.$store.commit('SET_ROOM_PROPERTY', ['connected', false]);
      this.$store.commit('SET_ROOM_PROPERTY', ['ready', false]);
    },
  },
};

</script>

